export class JobItemInformationReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'JobItemInformationReport';
		this.viewId = 'JobItemInformationReport';
		this.context_menu_name = $.i18n._( 'Task Information' );
		this.navigation_label = $.i18n._( 'Saved Report' );
		this.view_file = 'JobItemInformationReportView.html';
		this.api = TTAPI.APIJobItemInformationReport;
	}

	getCustomContextMenuModel() {
		return { include: ['default'] };
	}

}